<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process177"></a>

<p><br></p>
<p><span class="rvts17">商人恩雅（</span><span class="rvts50">Eithne the Trader</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:170,Y:227</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：商人恩雅</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">带给恩雅想要的那本</span><span class="rvts20"></span><span class="rvts21">尸体爆炸</span><span class="rvts20"></span><span class="rvts21">技能书</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1184.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">在一处废墟中，可以找到亡灵商人恩雅。恩雅出售死灵系技能书，首先她会询问你是站在黑环还是神圣教派一方。</span>
<br><span class="rvts21">只能回答</span><span class="rvts20"></span><span class="rvts21">我支持达利斯的神圣教派</span><span class="rvts20"></span><span class="rvts21">才可以通过该语言测试，无论选择的是中立，或者是支持黑环教派，都会引起她与你敌对并进入战斗。</span>
<br><span class="rvts21">通过友好的语言测试后，恩雅会请求你帮她找到一本</span><span class="rvts20"></span><span class="rvts21">尸体爆炸</span><span class="rvts20"></span><span class="rvts21">的技能书。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1185.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">这本书在该章节地图上几乎很少有人卖，可以通过一本</span><span class="rvts20"></span><span class="rvts21">土元素技能书</span><span class="rvts20"></span><span class="rvts21">和一本</span><span class="rvts20"></span><span class="rvts21">死灵技能书</span><span class="rvts20"></span><span class="rvts21">合成</span><span class="rvts20"></span><span class="rvts21">尸体爆炸技能书</span><span class="rvts20"></span><span class="rvts21">。</span>
<br><span class="rvts21">但是一定要注意，两本用于合成的技能书，等级需要一致，并且都得是不消耗源力的技能书才行。</span>
<br><span class="rvts21">然后找到合成好的技能书，卖给恩雅，该任务将暂时告一段落。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1186.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">之后在石碑花园的一处石棺房处，可以找到恩雅的尸骨（坐标</span><span class="rvts20">X:566,Y:133</span><span class="rvts21">），到达此地后该任务将关闭。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



